body {
    font-size: 14px !important;
  }
  .table td {
    vertical-align: top !important;
    border-top: 1px solid #dee2e6;
    padding-bottom: 0 !important;
    padding-top: .75rem !important;
  }
  .nav-link{
    color: black !important;
    font-weight: 500;
  }
  .nav-link:hover{
    color: grey !important;
  }
  .title-section{
    text-align: center;
    font-weight: 700;
  }
  .table-hover tbody tr:hover {
    color: #212529;
    background-color: #eff0ef !important;
  }
  .price-up {
    background-color: #7eff94;
  }
  .price-down {
    background-color: #f7c2bf;
  }
  
  .change24-up {
    color: #209902;
  }
  .change24-down {
    color: #ff2020;
  }

  .up{
    background: #e0ffe0;
  }

  .down {
    background: #f3e1e1;
    transition: ease-in-out 1s;
  }